.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.row {
			gap: 3rem;
		}
	}
	&::before {
		position: absolute;
		inset: 0;
		background-image: url(../images/tekstur.png);
		content: "";
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 340px;
		transform: translateY(50%);
		@include max-lg {
			width: 220px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 340px;
		transform: translate(30%, -50%);
		@include max-lg {
			width: 220px;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		border-radius: 1000px 1000px 10px 10px;
		padding: 8rem 1rem 3rem;
		@include max-lg {
			margin-top: -8rem;
			padding-top: 5rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		@include min-lg {
			display: none;
		}
		@include max-md {
			aspect-ratio: 3/4;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
