.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 160px;
		transform: translate(-40%, -50%) scaleX(-1);
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 160px;
		transform: translate(40%, -50%);
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			padding-top: 3rem;
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: 10px;
				color: var(--color-dark);
				.card-body {
					padding: 0 2rem 3rem;
					@include min-lg {
						padding: 0 1rem 2.5rem;
					}
					@include max-md {
						padding: 0 1rem 2rem;
					}
					.ribbon-venue {
						position: relative;
						width: fit-content;
						padding: 1rem;
						background-color: var(--color-primary);
						border-radius: 10px;
						transform: translateY(-50%);
						margin: 0 auto;
						svg {
							width: 40px;
							height: auto;
							path {
								stroke: var(--text-button);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem 1rem 0;
		border-radius: 10px;
		background-color: var(--color-primary);
		color: var(--color-dark);
		text-align: center;
		@include max-md {
			padding: 2rem 0.75rem 0;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		border-radius: 10px;
		padding: 1rem;
		background-color: var(--color-main);
		color: var(--color-dark);
		transform: translateY(50%);
		@include min-lg {
			gap: 0.25rem;
		}
		@include max-lg {
			margin-top: 2rem;
		}
		@include max-md {
			margin-top: 0;
			padding: 1rem 0;
		}
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3.5rem;
		@include max-lg {
			gap: 0;
		}
		@include max-md {
			gap: 3.75rem;
		}
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-button-trans);
		color: var(--color-button-trans);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary) !important;
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
	.maps-embed {
		width: 100%;
		height: auto;
		position: relative;
		aspect-ratio: 16/9;
	}
}
