.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	&::before {
		position: absolute;
		inset: 0;
		background-image: url(../images/tekstur.png);
		content: "";
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(10%, -20%);
		@include max-lg {
			transform: translate(10%, 10%);
		}
		@include max-md {
			transform: translate(10%, -10%);
		}
	}
	.orn-2 {
		position: absolute;
		left: 0;
		bottom: 3rem;
		width: 160px;
		transform: scaleX(-1) translateX(50%);
	}
	.orn-3 {
		position: absolute;
		width: 160px;
		right: 0;
		bottom: 3rem;
		transform: translateX(50%);
	}
	.and {
		@include max-lg {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
		}
		@include max-md {
			position: relative;
			inset: unset;
			transform: none;
		}
		margin: 2.5rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.couple-wrap {
		position: relative;
		background-color: var(--color-primary);
		border-radius: 1000px 1000px 10px 10px;
		padding: 8rem 1rem 2rem;
		@include max-lg {
			padding: 8rem 2rem 3rem;
		}
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
	}
	.image-wrap {
		width: 70%;
		margin: auto;
		position: relative;
		margin-bottom: 1.25rem;
		aspect-ratio: 3/3.5;
		border-radius: 1000px 1000px 10px 10px;
		margin-bottom: -5rem;
		@include max-md {
			width: 80%;
		}
		.couple-image {
			height: 100%;
			object-fit: cover;
			position: relative;
			width: 100%;
			border-radius: 1000px 1000px 10px 10px;
			z-index: 5;
		}
	}
	.couple {
		position: relative;
		background-color: var(--color-primary);
		border-radius: 10px;
		padding: 6rem 0.75rem 1rem;
		z-index: 2;
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
