.wishes-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	&::before {
		position: absolute;
		inset: 0;
		top: 30%;
		background-image: url(../images/tekstur.png);
		content: "";
	}
	.orn-1 {
		position: absolute;
		right: 0;
		top: 4rem;
		width: 160px;
		transform: translateX(25%);
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.card-form {
		margin-bottom: 0;
		background-color: var(--color-primary);
		position: relative;
		z-index: 3;
		border-radius: 1000px 1000px 10px 10px;
		&.card {
			border: none;
		}
		.card-body {
			padding: 8rem 1rem 2.5rem;
			@include max-md {
				padding: 5rem 1rem 2rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 0 0 3rem;
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				.wishes {
					text-align: left;
					height: 23.05rem;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
