.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	color: var(--color-dark);
	&::before {
		position: absolute;
		inset: 0;
		background-image: url(../images/tekstur.png);
		content: "";
	}
	.image-wrapper {
		position: absolute;
		inset: 0;
		&::after {
			position: absolute;
			inset: 0;
			background: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		@include min-lg {
			display: none;
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 140px;
		@include max-md {
			width: 100px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		@include max-md {
			width: 100px;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			position: relative;
			padding: 4rem 0 3rem;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			@include max-lg {
				color: var(--color-light);
				justify-content: space-between;
			}
			.header-title {
				position: relative;
				text-align: center;
				width: 100%;
				@include min-lg {
					display: none;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.image-wrapper {
		position: absolute;
		inset: 0;
		@include min-lg {
			display: block;
		}
		&::after {
			position: absolute;
			inset: 0;
			background: rgba($color: #000000, $alpha: 0.4);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		.header {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			padding: 4rem 0 3rem;
			.header-title {
				margin-bottom: 0;
				padding: 5rem 0;
				bottom: 0;
				@include min-lg {
					display: block;
				}
				@include max-md {
					padding: 3rem 0;
				}
			}
		}
	}
}
