.gallery-section {
	position: relative;
	padding: 3rem 0 5rem;
	@include max-md {
		padding: 3rem 0;
	}
	.zoom-gallery {
		width: 100%;
		position: relative;
		display: grid;
		grid-template-columns: auto auto auto auto;
		// @include min-lg {
		// 	grid-template-columns: auto auto auto;
		// }
		@include max-md {
			padding: 0;
			grid-template-columns: auto auto auto auto;
		}
		a {
			position: relative;
			background-color: var(--color-secondary);
			padding: 2px;
			&.grid-lg {
				grid-column: span 2;
			}
			&::before {
				position: absolute;
				content: "";
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: transparent;
				z-index: 2;
				transition: all 0.5s ease-in-out;
			}
			&::after {
				position: absolute;
				font-family: "Font Awesome 6 Free";
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				content: "\2b";
				transition: all 0.4s ease-in-out;
				color: #f4f4f4;
				padding: 2px 8px;
				font-size: 20px;
				border-radius: 1000px;
				font-weight: 700;
				border: solid 2px #f4f4f4;
				opacity: 0;
				z-index: 3;
			}
			&:hover {
				&::before {
					background-color: rgba($color: #000000, $alpha: 0.25);
				}
				&::after {
					opacity: 1;
				}
			}
		}
		.photo {
			width: 100%;
			object-fit: cover;
			position: relative;
			height: auto;
			aspect-ratio: 3/4;
		}
	}
}
